.container1{
    display: flex;
}
.sidebar{
    background: rgb(73, 185, 250);
    color: #fff;
    height: 100vh;
    width: 300px;
}
main{
    width: 100%;
    height: 94vh;
    overflow: scroll;
    padding: 20px;
    transition: 0.1s;
    background: rgb(7, 157, 244);
}
.top_section{
    display: flex;
    align-items: center;
    padding: 20px 15px;
}
.logo{
    font-size: 40px;
    margin-top: 5px;
    cursor: pointer;
}
.bars{
    display: flex;
    font-size: 35px;
    cursor: pointer;
}
.link{
    display: flex;
    color: #fff;
    padding: 10px 15px ;
    gap: 15px;
    transition: all 0.5s;
}
.link:hover{
    background-color: lightskyblue;
    color: #000;
    transition: all 0.5s;
}
.active{
    background: lightskyblue;
    color: #000;
    margin-bottom: 3px;
    margin-top: 3px;
}
.icon,.link_text{
    font-size: 28px;
}
.link_text{
    text-decoration: none;
}
