*{
    text-decoration: none;
    user-select: none;
}
.container{
    height: 100vh;
    background: url(./pic1.jpg);
}
.form1{
    width: 400px;
    height: 500px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: rgba(255,255,255,0.2);
    border-radius: 20px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form1 h2{
    color: #fff;
    font-size: 50px;
    margin-top: -5px;
}
.form1 .imgcontainer1{
     width: 100px;
     height: 100px;
     border-radius: 50%;
     background: #fff;
     margin-top: -20px;
}
.form1 .imgcontainer1 img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 3px solid #fff;
    cursor:pointer;
}
.form1 .input{
    width: 320px;
    height:45px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 3px solid #fff;
}
.form1 .input input{
    position: absolute;
    width: 308px;
    height: 38px;
    padding-left: 10px;
    font-size: 25px;
    border: none;
    outline: none;
    background: transparent;
}
.form1 input::placeholder{
    color: #fff;
}
.form1 button{
    width: 130px;
    height: 40px;
    margin-top: 30px;
    border-radius: 20px;
    border: none;
}
.form1 h4{
    color: #fff;
    margin-top: 30px;
    font-size: 17px;
}
.form1 h4 a{
    color: red;
}