.card{
    width: 320px;
    height: 180px;
    border-radius: 15px;
    display: grid;
    margin-left: 20px;
    margin-top: 20px;
    box-shadow: 8px 8px 8px #000;
}
h1{
    color: #fff;
    margin-top: -10px;
    margin-left: 21px;
}
.k1{
    width: 100vw;
}
.cards{
    width: 100vw;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
}
.card h3{
    color: #fff;
    font-size:20px ;
    margin-top:150px ;
    margin-left: 20px;
    text-shadow: 0px 0px 3px #fff;
}
.card-1{
    background:linear-gradient(to  bottom, rgb(135, 55, 132), rgb(236, 139, 213));
    opacity: .9;
}
.card-2{
    background:linear-gradient(to  bottom, rgb(251, 37, 48), rgb(243, 105, 105));
    opacity: .9;
}
.card-3{
    background:linear-gradient(to  bottom, rgb(236, 240, 6), rgb(227, 251, 45));
    opacity: .9;
}
.card h4{
    color: #fff;
    font-size: 27px;
    margin-top: -120px;
    margin-left: 200px;
    text-shadow: 0px 0px 3.5px #fff;
}
.card h5{
    color: #fff;
    font-size: 18px;
    margin-top: -45px;
    margin-left: 200px;
    text-shadow: 0px 0px 3.5px #fff;
}
.card .cd{
    color: #fff;
    font-size: 30px;
    margin-top: -190px;
    margin-left: 250px;
    filter: drop-shadow(0px 0px 3px #fff);
}
.card img{
  width: 100px;
  height: 100px;
  margin-top: -180px;
  mix-blend-mode: multiply;
}

